"use client";
import React, { useState, useEffect } from 'react';
import { XIcon, UserIcon, MailIcon, PhoneIcon, CalendarIcon, CheckIcon } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';

export default function NewAdminModal({ onAdd, onClose, showModal }) {
  const [step, setStep] = useState(1);
  const [form, setForm] = useState({
    userId: '',
    password: 'test1234',
    userName: '',
    email: '',
    phone: '',
  });


  useEffect(() => {
    if (showModal) {
      setForm({
        userId: '',
        password: 'test1234',
        userName: '',
        email: '',
        phone: '',
      });
      setStep(1); // ‚úÖ Î™®Îã¨ Ïó¥Î¶¥ Îïå Ï≤´ Îã®Í≥ÑÎ°ú Ï¥àÍ∏∞Ìôî
    }
  }, [showModal]);

  const handleNext = () => setStep((prev) => Math.min(prev + 1, 3));
  const handleBack = () => setStep((prev) => Math.max(prev - 1, 1));
  const handleChange = (e) => {
    const { name, value } = e.target;
    setForm((f) => ({ ...f, [name]: value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    console.log("üö® ÏµúÏ¢Ö Ï†úÏ∂ú form:", form);
    try {
      await onAdd(form);     // ‚úÖ ÏÑ±Í≥µ ÏãúÎßå Î™®Îã¨ Îã´Í∏∞
      onClose();
    } catch (error) {
      console.error("‚ùå Îì±Î°ù Ïã§Ìå®:", error);
      alert("Îì±Î°ùÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî.");
      // Î™®Îã¨ÏùÄ Îã´ÌûàÏßÄ ÏïäÏùå
    }
  };

  return (
      <AnimatePresence>
        {showModal && (
            <motion.div
                className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-40"
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                exit={{ opacity: 0 }}
            >
              <motion.form
                  className="grid gap-4 p-6 bg-white shadow-xl rounded-2xl w-80"
                  initial={{ scale: 0.8, opacity: 0 }}
                  animate={{ scale: 1, opacity: 1 }}
                  exit={{ scale: 0.8, opacity: 0 }}
                  transition={{ type: 'spring', stiffness: 260, damping: 20 }}
                  onSubmit={handleSubmit}
              >
                {/* Ìó§Îçî */}
                <div className="flex items-center justify-between">
                  <div className="flex items-center space-x-2">
                    <UserIcon size={24} className="text-[#9BCC47]" />
                    <h3 className="text-xl font-bold text-gray-800">Ïã†Í∑ú Í¥ÄÎ¶¨Ïûê Îì±Î°ù</h3>
                  </div>
                  <button type="button" onClick={onClose} className="p-1 rounded-full hover:bg-gray-100">
                    <XIcon size={20} className="text-gray-600" />
                  </button>
                </div>

                {/* Îã®Í≥Ñ ÌëúÏãú */}
                <div className="flex items-center justify-between">
                  {[1, 2, 3].map((i) => (
                      <div key={i} className="flex-1 h-1 mx-1 bg-gray-200 rounded">
                        <motion.div
                            className="h-full rounded bg-[#9BCC47]"
                            initial={false}
                            animate={{ width: step >= i ? '100%' : '0%' }}
                        />
                      </div>
                  ))}
                </div>

                {/* Step 1 */}
                {step === 1 && (
                    <div className="space-y-3">
                      <label className="flex items-center">
                        <UserIcon size={18} className="mr-2 text-gray-500" />
                        <input
                            name="userId"
                            value={form.userId}
                            onChange={handleChange}
                            type="text"
                            placeholder="ÏïÑÏù¥Îîî"
                            className="w-full p-2 border border-gray-300 rounded"
                            required
                        />
                      </label>
                      <label className="flex items-center">
                        <UserIcon size={18} className="mr-2 text-gray-500" />
                        <input
                            name="userName"
                            value={form.userName}
                            onChange={handleChange}
                            type="text"
                            placeholder="Ïù¥Î¶Ñ"
                            className="w-full p-2 border border-gray-300 rounded"
                            required
                        />
                      </label>
                    </div>
                )}

                {/* Step 2 */}
                {step === 2 && (
                    <div className="space-y-3">
                      <label className="flex items-center">
                        <MailIcon size={18} className="mr-2 text-gray-500" />
                        <input
                            name="email"
                            value={form.email}
                            onChange={handleChange}
                            type="email"
                            placeholder="Ïù¥Î©îÏùº"
                            className="w-full p-2 border border-gray-300 rounded"
                            required
                        />
                      </label>
                      <label className="flex items-center">
                        <PhoneIcon size={18} className="mr-2 text-gray-500" />
                        <input
                            name="phone"
                            value={form.phone}
                            onChange={handleChange}
                            type="tel"
                            placeholder="Ï†ÑÌôîÎ≤àÌò∏"
                            className="w-full p-2 border border-gray-300 rounded"
                        />
                      </label>
                    </div>
                )}

                {/* Step 3 */}
                {step === 3 && (
                    <div className="space-y-3">
                      <label className="flex items-center">
                        <span className="w-20">ÎπÑÎ∞ÄÎ≤àÌò∏:</span>
                        <input
                            name="password"
                            value={form.password}
                            onChange={handleChange}
                            type="password"
                            placeholder="ÎπÑÎ∞ÄÎ≤àÌò∏"
                            className="w-full p-2 border border-gray-300 rounded"
                            required
                        />
                      </label>
                    </div>
                )}


                {/* Î≤ÑÌäº */}
                <div className="flex justify-between mt-4">
                  {step > 1 ? (
                      <button type="button" onClick={handleBack} className="px-4 py-2 border rounded-md">
                        Îí§Î°ú
                      </button>
                  ) : <div />}
                  {step < 3 ? (
                      <button type="button" onClick={handleNext} className="px-4 py-2 bg-[#9BCC47] text-white rounded-md">
                        Îã§Ïùå
                      </button>
                  ) : (
                      <button type="submit" className="flex items-center px-4 py-2 bg-[#9BCC47] text-white rounded-md">
                        <CheckIcon size={16} className="mr-1" /> Îì±Î°ù
                      </button>
                  )}
                </div>
              </motion.form>
            </motion.div>
        )}
      </AnimatePresence>
  );
}
